@use "global" as *;

.about-us__inner {
  position: relative;
}

.about-us__content-wrapper {
  position: relative;
  margin-top: rem(28);
  @include mq("md") {
    margin-top: rem(38.4);
  }
}

.about-us__img-left {
  width: 100%;
  max-width: 37%;
  top: 0;
  position: absolute;
  z-index: 1;

  @include mq("md") {
    max-width: 34%;
    max-width: rem(400);
  }
}

.about-us__img-left img {
  aspect-ratio: 128 / 194;
  object-fit: cover;
  @include mq("md") {
    aspect-ratio: 400 / 606;
  }
}

.about-us__img-right {
  width: 100%;
  max-width: 82%;
  margin-left: auto;
  padding-top: rem(25.6);

  @include mq("md") {
    padding-top: rem(80);
    max-width: rem(880);
  }
}

.about-us__img-right img {
  aspect-ratio: 281 / 186;
  object-fit: cover;
  @include mq("md") {
    aspect-ratio: 880 / 581;
  }
}

.about-us__contents {
  z-index: 2;
  margin-top: rem(38);
  @include mq("md") {
    margin-left: rem(81);
    width: 100%;
    max-width: rem(840);
    position: absolute;
    top: 48.2%;
    left: 49.8%;
    margin-top: rem(0);
    transform: translate(-50%, -50%);
  }
}

.about-us__message {
  @include mq("md") {
    display: flex;
    justify-content: space-between;
    gap: rem(40);
  }
}

.about-us__message-main {
  font-size: rem(40);
  line-height: 1;
  color: $accent-green;

  @include mq("md") {
    color: $white;
    font-size: rem(80);
    line-height: 1.25;
  }
}

.about-us__message-main span {
  text-transform: uppercase;
}

.about-us__message-sub-wrapper {
  margin-top: rem(18);
  @include mq("md") {
    margin-top: 0;
  }
}

.about-us__button {
  margin-top: rem(38.2);
  text-align: center;
  @include mq("md") {
    margin-top: rem(38);
  }
}

.about-us__illust {
  display: none;
  @include mq("md") {
    display: block;
    width: 100%;
    max-width: rem(194);
    position: absolute;
    left: clamp(rem(20), 78.763889vw, rem(1009));
    bottom: rem(-91);
  }
}
