.button::after {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 100%;
  border: 1px solid #408f95;
}

.button__animation {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  padding: 1rem 5.6rem 0.9rem 2.3rem;
  font-size: 0.875rem;
  border: 1px solid #408f95;
  color: #ffffff;
  background-color: #408f95;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.button__animation::first-letter {
  text-transform: capitalize;
}

.button__animation::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  transform: translateX(-100%);
  transition: all 0.3s;
  z-index: -1;
}

.button:hover .button__animation::before {
  transform: translateX(0);
}

.button:hover .button__animation {
  color: #408f95;
}

.button__animation::after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 0.5rem;
  top: 41%;
  right: 1.2375rem;
  transform: translate(-50%);
  background-image: url("../images/common/arrow.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.button:hover .button__animation::after,
.button.button--padding:hover .button__animation::after {
  background-image: url("../images/common/arrow-green.png");
}

.button.button--padding .button__animation {
  padding: 1.1rem 7rem 0.9rem 3.2625rem;
}

.button.button--padding .button__animation::after {
  right: 2.2375rem;
  top: 37.8%;
}

.button.button--padding input {
  padding: 0;
  color: #ffffff;
}

.button.button--padding input::after {
  right: 2.3375rem;
}

.button.button--padding:hover input {
  color: #408f95;
}

.button.button--white::after {
  border: 1px solid #ffffff;
}

.button.button--white span {
  color: #408f95;
  background-color: #ffffff;
  border: 1px solid #ffffff;
}

.button.button--white .button__animation {
  padding: 1rem 6rem 0.9rem 2.3rem;
}

.button.button--white .button__animation::before {
  background-color: #408f95;
}

.button.button--white:hover .button__animation {
  color: #ffffff;
}

.button.button--white .button__animation::after {
  background-image: url("../images/common/arrow-green.png");
}

.button.button--white:hover .button__animation::after {
  background-image: url("../images/common/arrow.png");
}

.button.button--white .button__uppercase {
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid transparent;
}

.button.button--white:hover .button__uppercase {
  color: white;
