@use "global" as *;

.price {
  background-color: $green2;
}

.price__inner {
  position: relative;
}

.price__img {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  @include mq("md") {
    width: 100%;
    max-width: rem(492);
    margin-left: rem(93);
  }
}

.price__img img {
  aspect-ratio: 345 / 227;
  object-fit: cover;
  @include mq("md") {
    aspect-ratio: 492 / 746;
  }
}

.price__wrapper {
  margin-top: rem(24);
  @include mq("md") {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: rem(39);
  }
}

.price__lists-box {
  width: 100%;
  margin-top: rem(43.6);
  @include mq("md") {
    margin-top: 0;
    max-width: rem(492);
  }
}

.price__lists:not(:first-child) {
  margin-top: rem(42);
  @include mq("md") {
    margin-top: rem(39);
  }
}

.price__button {
  margin-top: rem(39);
  text-align: center;
  @include mq("md") {
    margin-top: rem(79);
  }
}

.price__illust {
  display: none;
  @include mq("md") {
    display: inline-block;
    position: absolute;
    width: 100%;
    max-width: rem(437);
    bottom: rem(-42);
    right: clamp(rem(400), 58.055556vw, rem(749));
  }
}

.price__illust img {
  aspect-ratio: 437 / 201;
  object-fit: cover;
}
