@use "global" as *;

.voice-card {
  display: flex;
  flex-direction: column;
  padding: rem(17.6) rem(15) rem(20);
  box-shadow: 2px 2px 10px 0 rgba($color: #000000, $alpha: 0.25);
  @include mq("md") {
    padding: rem(24) rem(24) rem(21) rem(24);
  }
}

.voice-card__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.voice-card__top {
  width: 100%;
  max-width: rem(276);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-top: auto;
}

.voice-card__label {
  @include mq("md") {
    align-items: end;
    margin-top: auto;
    display: flex;
    justify-content: space-between;
  }
}

.voice-card__age {
  font-size: rem(12);
  font-weight: $normal;
  line-height: 1.5;
  color: $green;
}

.voice-card__category {
  margin-left: auto;
}

.voice-card__title {
  margin-top: rem(4);
  font-size: rem(16);
  line-height: 1.5;
  color: $green;
  border-bottom: 1px solid $accent-green;
  font-weight: $medium;
  flex-grow: 1;
  @include mq("md") {
    font-size: rem(24);
    margin-top: rem(8);
  }
}

.voice-card__img {
  margin-left: rem(13);
  width: 100%;
  max-width: rem(180);
}

.voice-card__img img {
  aspect-ratio: 151 /117;
  object-fit: cover;
}

.voice-card__text {
  margin-top: rem(19);
  @include mq("md") {
    margin-top: rem(43);
  }
}
