@use "global" as *;

.mv {
  width: 100%;
  height: 100dvh;
  min-height: rem(400);
}

.mv__inner {
  position: relative;
  width: 100%;
  height: inherit;
  min-height: rem(400);
}

.mv__slider {
  height: inherit;
  min-height: rem(400);
}

.mv .swiper {
  height: inherit;
}

.mv .swiper-wrapper,
.mv .swiper-slide {
  height: inherit;
}

.mv__title {
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 49.12%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $white;
  text-align: center;
  @include mq("md") {
    top: 49.18%;
  }
}

.mv__title-large {
  font-size: rem(40);
  text-transform: uppercase;
  font-family: $second-font-family;
  font-weight: normal;
  letter-spacing: 0.53em;
  text-indent: 0.53em;
  @include mq("md") {
    font-size: rem(80);
  }
}

.mv__title-small {
  font-size: rem(20);
  line-height: 1;
  margin-top: rem(-3);
}
